h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-family: 'Noto Sans', sans-serif;
}
h1 {
	font-size: 28px;
}
h2 {
	font-size: 20px;
}
p {
	font-size: 12px;
}

.loading-screen {
	width: 20px;
	height: 20px;
	bottom: 4.5%;
	right: 3%;
	position: fixed;
	display: block;
}
.loading-screen .spinner {
	bottom: 0%;
	right: 0%;
	position: absolute;
	transform: translate(-50%, -50%);
	width: 20;
	height: 20px;
}

.loading-screen .spinner:before {
	content: '';
	box-sizing: border-box;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 50px;
	height: 50px;
	margin-top: -10px;
	margin-left: -10px;
	border-radius: 50%;
	border: 2px solid transparent;
	border-top-color: #07d;
	border-bottom-color: #07d;
	animation: spinner .8s ease infinite;
}

.content {
	position: relative;
}

a.active {
	color: red;
}

p#copyright {
	font-style: italic;
	color: #5a5a5a;
}

#Colors #color-box {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
#Colors #color-box .color-item {
	display: block;
	position: relative;
	border-radius: 10px;
	height: 100px;
	width: 100px;
	margin: 5px;
	cursor: pointer;
}
#Colors #color-box .color-item.color-creator {
	height: 100px;
	width: 100px;
	background: #ececec;
	text-align: center;
	vertical-align: middle;
	line-height: 100px;
	color: #5a5a5a;
	font-size: 32px;
}
#Colors #color-box .color-item.color-creator:hover {
	cursor: pointer;
	color: #ececec;
	background: #5a5a5a;
}
#Colors #color-box .color-item .color-item-randomize {
	display: block;
	height: 96px;
	width: 60px;
	padding-left: 20px;
	text-align: center;
	line-height: 100px;
	color: #ffffff;
	font-size: 32px;
}
#Colors #color-box .color-item .color-item-delete {
	position: absolute;
	height: 20px;
	width: 20px;
	line-height: 25px;
	top: 0;
	right: -2px;
}

@keyframes spinner {
	to {
		transform: rotate(360deg);
	}
}
